<template>
  <div class="register">
    <div class="register__img">
      <img
        src=" https://markdown-1253389072.cos.ap-nanjing.myqcloud.com/202202221429453.png"
        alt=""
        class="register__img__img"
      />
    </div>
    <div class="register__input">
      <input
        placeholder="请输入手机号"
        type="text"
        class="register__input__item register__input__username"
        v-model="phone"
      />
      <input
        placeholder="请输入密码"
        type="password"
        class="register__input__item register__input__password"
        v-model="password"
      />
      <input
        placeholder="确认密码"
        type="password"
        class="register__input__item register__input__confirm-password"
        v-model="confirmPassword"
      />
    </div>
    <div class="register__button">
      <button @click="handleregister" class="register__button__register">
        注册
      </button>
    </div>
    <div class="register__help">
      <a href="javascript:;" @click="goLogin" class="register__help__a"
        >已有帐号去登陆</a
      >
    </div>
  </div>
  <Toast v-show="isShow" :message="toastMessage" />
</template>

<script>
import { reactive, toRefs } from "vue";
import { useRouter } from "vue-router";
import { post } from "../../utils/request";
import Toast, { showToastEffect } from "@/components/Toast.vue";

const userRegisterEffect = (showToast) => {
  const data = reactive({
    phone: "",
    password: "",
    confirmPassword: "",
  });

  const router = useRouter();

  const handleregister = async () => {
    try {
      if (data.phone == "") {
        showToast("手机号不能为空!!!");
        return;
      }

      if (data.password !== data.confirmPassword) {
        showToast("密码不一致!!!");
        return;
      }

      if (data.password == "") {
        showToast("密码不能为空!!!");
        return;
      }

      const result = await post("/user/register", {
        phong: data.phone,
        password: data.password,
      });

      if (result.data.code == "000") {
        showToast("注册成功!!!");
        setTimeout(() => {
          router.push({ name: "Login" });
        }, 2000);
      } else {
        showToast("登陆失败,用户名或密码不正确!!!");
      }
    } catch (error) {
      showToast("发送请求失败!!!");
      console.log(error);
    }
  };

  const { phone, password, confirmPassword } = toRefs(data);

  return { phone, password, confirmPassword, handleregister };
};

const userLoginEffect = () => {
  const router = useRouter();
  const goLogin = () => {
    router.push({ name: "Login" });
  };
  return { goLogin };
};

export default {
  name: "register",
  components: { Toast },

  setup() {
    const { isShow, toastMessage, showToast } = showToastEffect();
    const { phone, password, confirmPassword, handleregister } =
      userRegisterEffect(showToast);
    const { goLogin } = userLoginEffect();

    return {
      handleregister,
      goLogin,
      phone,
      password,
      confirmPassword,
      isShow,
      toastMessage,
    };
  },
};
</script>

<style lang="scss" scoped>
.register {
  text-align: center;

  &__img {
    &__img {
      width: 66rem;
      height: 66rem;
      margin-top: 120rem;
    }
  }

  &__input {
    margin-top: 40rem;
    margin-bottom: 16rem;

    &__item {
      width: 295rem;
      height: 48rem;
      background: #f9f9f9;
      border: 1rem solid rgba(0, 0, 0, 0.1);
      border-radius: 6rem;
      margin-bottom: 16rem;
      font-family: PingFangSC-Regular;
      font-size: 16rem;
      text-indent: 1em;
    }
  }

  &__button {
    margin-bottom: 16rem;

    &__register {
      width: 295rem;
      height: 48rem;
      background: #0091ff;
      box-shadow: 0 4rem 8rem 0 rgba(0, 145, 255, 0.32);
      border-radius: 4rem;
      font-family: PingFangSC-Regular;
      font-size: 16rem;
      color: #ffffff;
    }
  }

  &__help {
    font-family: PingFangSC-Regular;
    font-size: 14rem;
    color: rgba(0, 0, 0, 0.5);
    letter-spacing: 0;

    &__a {
      color: rgba(0, 0, 0, 0.5);
    }
  }
}
</style>
